.contact-us {
  padding: 80px 0 0;
  grid-column: 1 / -1;

  @include onTablet {
    padding: 120px 0 0;
    grid-column: 1 / -1;
  }

  @include onDesktop {
    padding: 180px 0 0;
    grid-column: 7 / 12;
  }

  @include onBigDesktop {
    grid-column: 8 / 13;
  }

  &__title {
    color: #fff;
    font-family: Poppins, Arial, sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;
    margin: 0 0 48px;

    @include onTablet {
      margin: 0 0 56px;
    }
  }

  &__calls-us {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 48px;
    }
  }

  &__calls-us-title {
    margin: 0;
    font-family: Poppins, Arial, sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 19, 5px;
    letter-spacing: 3px;
    color: #959595;
    margin-bottom: 8px;
  }

  &__number {
    color: #fff;
    text-decoration: none;
    font-family: 'Open Sans', Arial, sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;

    &:hover {
      color: #959595;
    }
  }
}

.visit-us {
  margin-bottom: 40px;

  @include onTablet {
    margin-bottom: 48px;
  }

  &__title {
    margin: 0;
    font-family: Poppins, Arial, sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 19, 5px;
    letter-spacing: 3px;
    color: #959595;
    margin-bottom: 8px;
    width: 100%;

    @include onTablet {
      width: 70%;
    }
  }

  &__description {
    color: #fff;
    text-decoration: none;
    font-family: 'Open Sans', Arial, sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    &:hover {
      color: #959595;
    }
  }
}

.socials {
  &__title {
    margin: 0;
    font-family: Poppins, Arial, sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 19, 5px;
    letter-spacing: 3px;
    color: #959595;
    margin-bottom: 16px;
  }

  &__icons {
    right: 32px;
    gap: 32px;
    display: flex;
    :hover {
      cursor: pointer;
      opacity: 0.7;
    }
  }
}
