.header {
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #2c2c2c;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  height: 100vh;

  &__top {
    margin-top: 18px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    @include onTablet {
      padding: 0 41px 0 20px;
    }
  }

  &__icon {
    display: flex;
    gap: 32px;
    align-items: center;
  }

  &__menu {
    margin-top: 18px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    gap: 32px;
    align-items: center;
    margin-bottom: 32px;
  }

  &__strategic-agency {
    padding: 48px 20px 0;

    @include pageGrid;

    @include onTablet {
      padding: 56px 41px 0;
    }

    @include onDesktop {
      padding: 48px 54px 205px;
    }
  }

  &__title {
    font-family: Poppins, sans-serif;
    font-weight: 600;
    font-size: 52px;
    line-height: 62.4px;
    color: #fff;
    margin: 0;
    grid-column: -1 / 1;

    @include onTablet {
      height: 100%;
      font-size: 62px;
      line-height: 68px;
      justify-content: flex-start;
      display: flex;
      text-align: left;
    }

    @include onDesktop {
      width: 200px;
      text-align: left;
    }
  }

  &__description {
    margin: 32px 0 0;
    font-family: Poppins, sans-serif;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: #e5e5e5;
    grid-column: -1 / 1;

    @include onTablet {
      grid-column: 1 / 5;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    margin-top: 72px;
    grid-column: -1 / 1;

    @include onTablet {
      grid-column: 1 / 4;
    }
  }
}

.waves {
  background-image: url(./images/Photo.png);
  margin-top: 18px;
  background-repeat: no-repeat;
  height: 50vh;
  background-position: center;
  border-radius: 30px;
  position: relative;
  background-size: cover;

  display: flex;
  flex-direction: column;
  padding: 24px 41px;
  box-sizing: border-box;
  top: 30px;

  @include onDesktop {
    padding: 24px 54px;
    margin-top: 38px;
    border-radius: 30px 0;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50%;
    height: calc(100vh - var(--header-height));
  }

  &__content {
    margin-top: auto; /* Опускає елемент вниз */
    width: 100%;
  }

  &__button {
    display: flex;
    gap: 16px;
  }

  &__button-first {
    width: 32px;
    height: 32px;
    border-radius: 100px;
    background-color: #fff;
    border: #fff;
    color: #253757;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-decoration: none;
    box-sizing: border-box;
  }

  &__button-first1:hover {
    background-color: #dbd1d1;
    transform: scale(1.1);
  }

  &__button-first:active {
    transform: scale(0.95);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }

  @keyframes pulse {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    50% {
      transform: scale(1.2);
      opacity: 0.7;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }

  &__button-first:hover {
    animation: pulse 1s infinite;
  }

  &__button-second {
    width: 32px;
    height: 32px;
    border-radius: 100px;
    background-color: #fff;
    border: #fff;
    color: #253757;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-decoration: none;
    box-sizing: border-box;
    transition: all 0.3s ease-in-out;
  }

  &__button-second:hover {
    background-color: #dbd1d1;
    transform: scale(1.1);
  }

  &__button-second:active {
    transform: scale(0.95);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }

  @keyframes pulse {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    50% {
      transform: scale(1.2);
      opacity: 0.7;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }

  &__title {
    margin: 16px 0 0;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 19px;
    color: #fff;
  }

  &__description {
    font-family: Open-Sans, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
  }
}

.main {
  margin-top: 40px;
}

.button {
  width: 100%;
  height: 50px;
  border-radius: 8px;
  background-color: var(--primary-color);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  text-decoration: none;
  box-sizing: border-box;
  border: none;
  cursor: pointer;
  transition:
    transform 0.3s ease,
    background-color 0.3s ease,
    box-shadow 0.3s ease;

  &:hover {
    background-color: #1040b6;
    transform: scale(1.01);
    box-shadow: 0 4px 15px rgba(1, 52, 171, 0.5);
  }

  &:active {
    transform: scale(0.98);
    box-shadow: 0 4px 15px rgba(1, 52, 171, 0.5);
  }
}
