.navigation {
  margin: 80px 0 0;
  padding-bottom: 80px;

  @include onTablet {
    margin: 149px 0 0;
  }

  @include onDesktop {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.footer__logo {
  margin-bottom: 48px;
  flex-direction: column;

  @include onTablet {
    margin-bottom: 24px;
  }

  @include onDesktop {
    flex-direction: row;
  }
}

.footer_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 24px;
  justify-content: space-between;

  @include onTablet {
    flex-direction: row;
    gap: 40px;
  }
}

.footer__link {
  position: relative;
  text-decoration: none;
  color: #fff;
  font-family: Poppins, Arial, sans-serif;
  font-weight: 700;
  font-size: 13px;
  letter-spacing: 3px;
  line-height: 19.5px;

  &:hover {
    color: var(--primary-color);
    text-decoration: none;
  }
}
